<template>
    <div class="wrap">
      <div class="jindu">
        <div class="span" :style="{width:widthP*100+'%'}"></div>
      </div>
      <div class="share" @click="share">分享</div>
    </div>
</template>

<script>
    export default {
      name: "collectPrize",
      data() {
        return {
          widthP: 0
        }
      },
      methods: {
        addPrecent () {
          this.widthP = 20/40;
        },
        share () {
          console.log('弹出分享提示')
        }
      },
      mounted() {
        this.addPrecent()
      }
    }
</script>

<style scoped>
.jindu {
  width: 100%;
}
  .span {
    background: pink;
    height: 20px;
  }
  .share {
    padding: 10px;
    text-align: center;
  }
</style>



